@import "reset.css";
:root {
    --app-header-height: 56px;
    --app-padding-width: 10%;
    --app-blue: #5fb7f8;
    --app-background-color: #f3f3f3;
}

body {
    background-color: var(--app-background-color);
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 56px;
    background: var(--app-blue);
    padding-left: 50px;
    padding-right: 50px;
}

.title {

    color: white;
}

.navigation ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.navigation ul li {
    margin-right: 10px;
    color: white;
}
.navigation ul li a {
    color: #fff;
    text-decoration: none;
    letter-spacing: 0.2px;
    transition: color 0.25s ease-in-out;
}
.navigation ul li a:hover {
    color: blue;
}
.section{
    height:calc(100vh - 56px - 120px);
}
div.banner {
    height: 200px;
    background: white;
    padding-left: 50px;
    padding-right: 50px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

    div.banner h1 {
        margin: 0;
    }

    div.banner p {
        margin: 10px 0;
    }

    div.banner div {
        margin-top: 10px;
    }

    div.banner button:first-child {
        border: 1px solid blue;
        background: white;
        color: blue;
        width:120px;
        height:50px;
        border-radius:6px;
    }

    div.banner button:last-child {
        background: blue;
        color: white;
        margin-left:10px;
        width: 120px;
        height: 50px;
        border-radius: 6px;
    }

.site-main-content-container {
    display: flex;
    justify-content: center;
    flex-wrap:wrap;
    padding-left: 50px;
    padding-right: 50px;
}
div.gallery {
    border: 1px solid #777;
    border-radius: 6px;
}

    div.gallery:hover {
        border: 1px solid #ccc;
    }

    div.gallery img {
        width: 100%;
        height: auto;
    }

div.desc {
    padding: 15px;
    text-align: center;
    color: #fff;
}

.responsive {
    margin-top: 10px;
    padding: 0 5px;
    float: left;
    width: 20%;
}

footer {
    height: 120px;
    background-color: #777;
    display: flex;
    justify-content: center;
    align-items: center;
    clear: both;
    
}

@media only screen and (max-width: 768px) {
    
    .responsive {
        width: 49.99999%;
        margin: 5px 0;
    }
}

@media only screen and (max-width: 500px) {
    .responsive {
        width: 100%;
    }
}